:root {
  /* Premium US Fintech Color System */
  --background: hsl(220, 26%, 14%);
  --foreground: hsl(210, 20%, 98%);
  --muted: hsl(220, 20%, 22%);
  --muted-foreground: hsl(215, 15%, 60%);
  --popover: hsl(220, 26%, 14%);
  --popover-foreground: hsl(210, 20%, 98%);
  --card: hsl(0, 0%, 99%);
  --card-foreground: hsl(220, 30%, 18%);
  --border: hsl(220, 15%, 28%);
  --input: hsl(220, 20%, 22%);
  
  /* Primary: Deep Navy - for main buttons and actions */
  --primary: hsl(220, 40%, 20%);
  --primary-foreground: hsl(0, 0%, 100%);
  
  /* Secondary: Slate - for secondary buttons */
  --secondary: hsl(220, 15%, 35%);
  --secondary-foreground: hsl(0, 0%, 100%);
  
  /* Accent: Muted Teal/Emerald - ONLY for success, CTAs, highlights */
  --accent: hsl(168, 55%, 42%);
  --accent-foreground: hsl(0, 0%, 100%);
  
  --destructive: hsl(0, 65%, 55%);
  --destructive-foreground: hsl(0, 0%, 100%);
  --ring: hsl(220, 40%, 40%);
  --radius: 0.625rem;
  
  /* Semantic Colors */
  --success: hsl(158, 50%, 42%);
  --success-light: hsl(158, 45%, 92%);
  --warning: hsl(38, 75%, 50%);
  --warning-light: hsl(38, 75%, 92%);
  --danger: hsl(0, 65%, 55%);
  --danger-light: hsl(0, 65%, 94%);
  
  /* Brand Colors */
  --navy: hsl(220, 40%, 18%);
  --navy-deep: hsl(220, 45%, 12%);
  --slate: hsl(220, 15%, 35%);
  --slate-light: hsl(220, 15%, 45%);
  --teal: hsl(168, 55%, 42%);
  --gold: hsl(42, 55%, 55%);
}

/* Glass Effect */
.glass-effect {
  background: rgba(255, 255, 255, 0.04);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.08);
}

/* Gradients - more subtle */
.gradient-accent {
  background: linear-gradient(135deg, var(--teal) 0%, hsl(180, 45%, 45%) 100%);
}

.gradient-navy {
  background: linear-gradient(135deg, var(--navy) 0%, var(--navy-deep) 100%);
}

.text-gradient {
  background: linear-gradient(135deg, var(--teal) 0%, hsl(180, 50%, 50%) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Typography */
.text-heading {
  color: var(--foreground);
  font-weight: 600;
  letter-spacing: -0.01em;
}

.text-body {
  color: var(--muted-foreground);
  line-height: 1.6;
}

